<template>
  <h2>Hello world</h2>
  <p>{{ connection }}</p>
  <ol>
    <li v-for="(message, i) in messages" :key="i">
      {{i}} {{message}}
    </li>
  </ol>
  <form action="wss://websocket-server.creepreplit.repl.co/" @submit.prevent="sendMessage">
    <input ref="textInput" type="text">
    <input type="submit">
  </form>
</template>

<script>
export default {
  data() {return {
    connection: null,
    messages: ["First", "Second"],
  }},
  mounted() {
    this.connection = new WebSocket("wss://websocket-server.creepreplit.repl.co/");
    this.connection.onmessage = (data) => {
      const msg = data.data
      this.messages.push(msg)
    }
    this.connection.onclose = () => {
      this.connection = null
    }
  },
  unmounted() {
    this.connection.close(1000);
  },
  methods: {
    sendMessage() {
      const textInput = this.$refs.textInput
      const msg = textInput.value
      textInput.value = ""
      this.connection.send(msg)
    }
  }
}
</script>

